<template lang="pug">
v-app
  v-container(v-if="!location")
    girder-auth(:oauth="true")
  v-container(v-else, fluid)
    .annotation-view
      tdm-app(
          :autoplay="false",
          :loading="loading",
          :tdm="tdm",
          :video-width="1920",
          :video-height="1080",
          :duration="300",
          :framerate="30",
          :color-by.sync="colorBy",
          :label-format="labelFormat",
          :meta="['name', 'type', 'actor_id', 'src_status']"
          :url="url")
        v-card.mb-2(slot="title")
          v-card-title(primary-title)
            div
              h3.headline Girder KPF Viewer
          v-card-text
            p Upload files named *.mp4, *.activities.yml, *.geom.yml, and *.types.yml to a folder
              | in your user space on data.kitware.com and browse to it through the data
              | browser below.  Use of private access controls is recommended.
              | You must be logged in on data.kitware.com.
          selection-widget.pa-2(
              :tdm="tdm",
              :color-by="colorBy",
              :color-map="colorMap",
              :state-map.sync="stateMap")
        div(slot="widget")
          v-card.my-2
            girder-browser(
                v-if="location",
                :location.sync="location",
                :new-folder-enabled="false",
                :new-item-enabled="false",
                :select-enabled="false")
</template>

<script>
import {
  Upload as GirderUpload,
  Authentication as GirderAuth,
  DataBrowser as GirderBrowser,
} from '@girder/components/src/components';
import KPF from 'stumpf-common/utils/kpf';
import { fromKPF } from 'stumpf-common/utils/tdmHelper';
import { colorByMeta, resetState, makeTdm } from 'stumpf-common/utils/tdm';
import { TdmApp, KpfWidget, SelectionWidget } from 'stumpf-common/components';

const targets = [
  'types.yml',
  'activities.yml',
  'geom.yml',
  '.mp4',
];

export default {
  components: {
    TdmApp,
    KpfWidget,
    SelectionWidget,
    GirderUpload,
    GirderAuth,
    GirderBrowser,
  },
  inject: ['girderRest'],
  data() {
    return {
      loading: false,
      tdm: makeTdm([], 0),
      colorBy: 'name',
      colorMap: null,
      stateMap: null,
      labelFormat: ['name', 'actor_id'],
      location: null,
      url: "",
    };
  },
  watch: {
    colorBy(newVal) {
      this.colorMap = colorByMeta(this.tdm, newVal);
      this.stateMap = resetState(this.tdm, newVal);
    },
    async location(newval) {
      if (newval._modelType !== 'folder') {
        return;
      }
      const { data: contents } = await this.girderRest.get('item', {
        params: { folderId: newval._id },
      });
      const hasTargets = targets.every(t => contents.some(el => el.name.indexOf(t) >= 0));
      if (hasTargets) {
        this.loadTargets(contents);
      }
    },
  },
  methods: {
    async addTracks(tracks) {
      this.tdm = makeTdm([].concat(...tracks, ...this.tdm.proxy.tracks), 0);
      this.colorMap = colorByMeta(this.tdm, this.colorBy);
      this.stateMap = resetState(this.tdm, this.colorBy);
    },
    async loadTargets(items) {
      this.loading = true;
      const kpfs = {};
      for (const item of items) {
        for (const target of targets) {
          if (item.name.indexOf(target) >= 0) {
            const { data: files } = await this.girderRest.get(`item/${item._id}/files`);
            if (target.indexOf('yml') >= 0) { 
              kpfs[target] = `file/${files[0]._id}/download`;
            } else if (target.indexOf('mp4') >= 0) {
              this.url = `${this.girderRest.apiRoot}/file/${files[0]._id}/download`;
            }
          }
        }
      }
      const { data: activities } = await this.girderRest.get(kpfs['activities.yml']);
      const { data: geom } = await this.girderRest.get(kpfs['geom.yml']);
      const { data: type } = await this.girderRest.get(kpfs['types.yml']);
      const kpf = KPF.fromText(activities, geom, type);
      this.tdm = makeTdm(fromKPF(kpf), 0);
      this.colorMap = colorByMeta(this.tdm, this.colorBy);
      this.stateMap = resetState(this.tdm, this.colorBy);
      this.loading = false;
    },
  },
  mounted() {
    if (this.girderRest.user) {
      this.location = {
        _modelType: 'user',
        _id: this.girderRest.user._id,
      };
    }
    this.girderRest.$on('login', () => {
      this.location = {
        _modelType: 'user',
        _id: this.girderRest.user._id,
      };
    });
  },
};
</script>
